import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';

export default {
	components: {
		uniLoadMore	
	},
	data() {
		return {
			titleNViewBackground: '',
			swiperCurrent: 0,
			swiperLength: 0,
			carouselList: [],
			typeList: [],
			insertList: [],
			productList: [],
			totalCount: 0,
			pageNo: 0,
			pageSize: 10,
			loadingType: 'more', //加载更多状态
		};
	},
	//加载更多
	onReachBottom(){
		this.loadDataMore();
	},
	onLoad() {
		this.loadData();
	},
	methods: {
		async loadData() {
			this.pageNo = 1;
			uni.request({
				url: this.baseUrl + '/api/home/list',
				data: {
					pageNo: this.pageNo,
					pageSize: this.pageSize,
				},
				header: {},
				success: (res) => {
					if (res.data.success) {
						let carouselList = res.data.result.headList;
						this.titleNViewBackground = carouselList[0].bgColor;
						this.swiperLength = carouselList.length;
						this.carouselList = carouselList;
						
						this.typeList = res.data.result.categoryList;
						this.insertList = res.data.result.insertList;
						this.productList = res.data.result.productList;
					}
				}
			});
		},
		async loadDataMore() {
			if(this.loadingType === 'nomore'){
				return;
			}
			this.loadingType = 'loading';
			this.pageNo ++;
			uni.request({
				url: this.baseUrl + '/api/home/random/product',
				data: {
					pageNo: this.pageNo,
					pageSize: this.pageSize,
				},
				header: {},
				success: (res) => {
					if (res.data.success) {
						console.log("res.data.result.total",res.data.result.total);
						this.productList = this.productList.concat(res.data.result.records);
						this.totalCount = res.data.result.total
						this.loadingType  = this.productList.length + 1 > this.totalCount ? 'nomore' : 'more';
					}
				}
			});
		},
		//轮播图切换修改背景色
		swiperChange(e) {
			const index = e.detail.current;
			this.swiperCurrent = index;
			this.titleNViewBackground = this.carouselList[index].bgColor;
		},
		//详情页
		navToDetailPage(item) {
			//测试数据没有写id，用title代替
			let id = item.id;
			console.log("详情页id", id);
			if(item.isList == '0'){
				uni.navigateTo({
					url: `/pages/product/product?id=${id}`
				})
			}else{
				uni.navigateTo({
					url: `/pages/ads/list?id=${id}`
				})
			}
			
		},
		moreSellWell(){
			this.$api.msg('点击了更多热销商品');
		},
	},

























}
